.emotions-container {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -40%);

    width: 900px;
    height: auto;
    text-align: center;
}

.emotions-container h3 {
    font-family: 'effra', sans-serif; 
    font-weight: 700; 
    font-style: normal; 
    font-size: 30px;
    margin: 0 0 40px 0;
}


@media screen and (max-width: 900px) {
    .emotions-container {
        top: 30%;
        transform: translate(-50%, -30%);
        width: 100%;
    }
}