body.emotion {
    transition: background-color 2s;
}

.emotion-container {
    position: absolute;
    top: 0px;
    left: 50%;
    transform: translateX(-50%);
    width: auto;
    height: 100%;
    text-align: center;

    /*padding-top: 110px;*/
}

.light .emotion-container {color: #ffffff}

.emotion-container .title {
    position: absolute;
    top: 45%;
    left: 50%;

    transform: translate(-50%, -50%);    
    
    /*padding-bottom: 110px;*/
}

.emotion-container h1 {font-family: 'effra', sans-serif; font-weight: 900; font-style: normal; font-size: 260px; letter-spacing: -14.04px; line-height: 1em; margin: 0; display:inline;}
.emotion-container h2 {font-family: 'effra', sans-serif; font-weight: 700; font-style: normal; font-size: 78px; letter-spacing: -0.31px; line-height: 0.6em; margin: 0;}
.emotion-container h3 {font-family: 'Kaushan Script', cursive; font-weight: 400; font-size: 89px; letter-spacing: -0.36px; line-height: 0.6em; margin: 0;white-space: nowrap;}
.emotion-container h3 i{font-size: 45px;}

.emotion-container .links {
    white-space: nowrap;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);  
    bottom: 75px;
}

.emotion-container .e-chip.share {
    display: none;
}

.emotion-container .footer { 
    top: auto;
    bottom: 20px;
}


