@media screen and (max-height: 725px) and (min-width: 500px) {
    .emotion-container {
        position: relative;
    }

    .emotion .footer {
        position: relative;
        top: auto;
        bottom: auto;
        padding-top: 35px;
        padding-bottom: 20px;
    }

    .emotion-container .title {
        position: relative;
        top: auto;
        left: auto;
        transform: none;
        padding-top: 110px;
        padding-bottom: 110px;
    }

    .emotion-container .links {
        position: relative;
        bottom: auto;
        left: auto;
        transform: none;;
    }
    
}

@media screen and (max-width: 700px) {
    .emotion-container{width: 95%;}

    .emotion-container .title {top: 40%;}

    .emotion-container .links {
        width: 100%;
        white-space: normal;
    }

    .emotion-container .e-chip.share {
        display: inline-block;

    }
    .emotion-container h1 {font-size: 208px; letter-spacing: -11.23px}
    .emotion-container h2 {font-size: 62px;}
    .emotion-container h3 {font-size: 71px;}
    .emotion-container h3 i{font-size: 36px;}

    .emotion-container .footer {
        position: absolute;
        margin-top: 0px;
        padding-bottom: 0px;
    }
}


@media screen and (max-width: 400px) {
    .emotion-container h1 {font-size: 130px; letter-spacing: -7.02px}
    .emotion-container h2 {font-size: 39px;}
    .emotion-container h3 {font-size: 44.5px;}
    .emotion-container h3 i{font-size: 22.5px;}

    .emotion-container .footer {
        position: absolute;
        margin-top: 0px;
        padding-bottom: 0px;
    }
}