.footer
{
    font-family: 'effra', sans-serif; 
    font-weight: 300; 
    font-style: normal; 

    font-size: 14px;

    position: fixed;
    bottom: 20px;
    width: 100%;
    text-align: center;
}

.light .footer, .light .footer a{color:#ffffff}

.footer a {
    font-size: 15px;
    margin-right: 25px;
}

.footer i {
    font-size: 11px;
}

@media screen and (max-width: 500px) {
    .footer a {margin-right: 0;}
    .footer span {
        display: block;
        margin-top: 4px;
    }
}